import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { FontLoader } from 'three/examples/jsm/loaders/FontLoader'
import { TextGeometry } from 'three/examples/jsm/geometries/TextGeometry.js';


/**
 * Base
 */
// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()
scene.background = new THREE.Color( 0x0d0c0c );

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 1
camera.position.y = 1
camera.position.z = 5
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true

/**
 * model
 */
 const cubeTextureLoader = new THREE.CubeTextureLoader()
 const environmentMapTexture = cubeTextureLoader.load([
    'texture/1/px.png',
    'texture/1/nx.png',
    'texture/1/py.png',
    'texture/1/ny.png',
    'texture/1/pz.png',
    'texture/1/nz.png'
])
let geo, center
// const axesHelper = new THREE.AxesHelper( 5 );
// scene.add( axesHelper );
var loader = new FontLoader();
const material = new THREE.MeshBasicMaterial(
    {
        color: 0x2c3e50,
        transparent: true,
        opacity: 0.8,
        side: THREE.DoubleSide
    } );
loader.load( 'fonts/helvetiker_regular.typeface.json', function ( font ) {

	const geometry = new TextGeometry( 'PROFILE', {
		font: font,
		size: 8,
		height: 1
	} );
    geometry.center()
    geo = geometry
    geometry.computeBoundingBox();
    center = geometry.boundingBox.getCenter(new THREE.Vector3());


    // const text = new THREE.Mesh(geometry, [
    //     new THREE.MeshPhongMaterial({ color: 0xad4000}),
    //     new THREE.MeshPhongMaterial({ color: 0x5c2301})
    // ])
    // scene.add(text)
    // console.log(text);
} );
//const geometry = new THREE.BoxGeometry( 1, 1, 5 );

// const cube = new THREE.Mesh( geometry, material );
// scene.add( cube );

const glassMaterial = new THREE.MeshPhysicalMaterial({  
    color: 0x3d3838,
    ior: 1,
    roughness: 0,   
    transmission: 1,  
    thickness: 0,
    reflectivity: 1,
    envMap: environmentMapTexture,
    depthWrite: false,
    transparent: true,
    opacity: 0.3
  });
let model
const gltfLoader = new GLTFLoader()
gltfLoader.load(
    'lightbulb.gltf',
    (gltf) =>
    {
        let pos1, pos2
        model = gltf.scene;
        model.traverse(function (child) {
            if(child.isMesh){
                child.material.envMap = environmentMapTexture
                child.material.metalness = 0
                child.material.envMapIntensity = 1
                
                child.castShadow = true
                child.receiveShadow = true

                if(child.name === "Sphere"){
                    child.material = glassMaterial
                }

                if(child.name === "insideglass"){
                    child.material = glassMaterial
                }

                if(child.name === "holder1"){
                    pos1 = child.position.clone()
                }
                if(child.name === "holder2"){
                    pos2 = child.position.clone()
                }

                child.material.needsUpdate = true
            }
        })
        model.traverse(function (child) {
            if(child.name === "light"){
                var mid = new THREE.Vector3();
                mid.addVectors(pos1, pos2);
                mid.multiplyScalar(0.5);
                child.geometry = geo
                child.material = [new THREE.MeshStandardMaterial({ emissive: new THREE.Color(0xffcc00, 5), metalness: 1}),
                    new THREE.MeshStandardMaterial({ emissive: new THREE.Color(0xff9900, 5)})]
                console.log(mid);
                child.position.set(mid.x, mid.y, mid.z)

                child.rotation.z = Math.PI 
                child.rotation.x = Math.PI *2
                child.updateMatrixWorld();
                child.rotation.y = Math.PI 

            }
        })
        model.name = "test"
        model.rotation.z = Math.PI / 5

        // console.log(model.position);
        // model.position.z = -1
    
        scene.add(model)
    }
)

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
    antialias: true,
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding
renderer.physicallyCorrectLights = true
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFShadowMap

/**
 * Animate
 */
const clock = new THREE.Clock()
let lastElapsedTime = 0

const tick = () =>
{
    const elapsedTime = clock.getElapsedTime()
    const deltaTime = elapsedTime - lastElapsedTime
    lastElapsedTime = elapsedTime

    // Update controls
    controls.update()

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()